<template>
  <div>
    <Table height='667' border size='small' :columns="columns7" :data="toBuyData">
    </Table>
    <Modal v-model="modal" title="编辑物品" @on-ok="ok" @on-cancel="cancel">
      <Form :model="formItem" :label-width="60">
        <Form-item label="物品名称">
          <Input v-model="formItem.name" placeholder="请输入"></Input>
        </Form-item>
        <Form-item label="预算价格">
          <Input v-model="formItem.price" placeholder="请输入"></Input>
        </Form-item>
        <Form-item label="实际价格">
          <Input v-model="formItem.priced" placeholder="请输入"></Input>
        </Form-item>
      </Form>
    </Modal>
  </div>
</template>
<script>
export default {
  props: ['toBuyData'],
  data() {
    return {
      modal: false,
      formItem: {
        name: '',
        price: '',
        priced: ''
      },
      columns7: [
        {
          title: '物品',
          key: 'name',
          align: 'center',
          width: '33%',
          filters: [
            {
              label: '客厅',
              value: 1
            },
            {
              label: '餐厅',
              value: 2
            },
            {
              label: '厨房',
              value: 3
            },
            {
              label: '阳台',
              value: 4
            },
            {
              label: '洗手间',
              value: 5
            },
            {
              label: '大房间',
              value: 6
            },
            {
              label: '小房间',
              value: 7
            },
            {
              label: '书房',
              value: 8
            }
          ],
          filterMultiple: false,
          filterMethod(value, row) {
            if (value === 1) {
              return row.select === '客厅'
            } else if (value === 2) {
              return row.select === '餐厅'
            } else if (value === 3) {
              return row.select === '厨房'
            } else if (value === 4) {
              return row.select === '阳台'
            } else if (value === 5) {
              return row.select === '洗手间'
            } else if (value === 6) {
              return row.select === '大房间'
            } else if (value === 7) {
              return row.select === '小房间'
            } else if (value === 8) {
              return row.select === '书房'
            }
          }

        },
        {
          title: '预算价格',
          key: 'price',
          align: 'center',
        },
        {
          title: '实际价格',
          key: 'priced',
          align: 'center',
        },
        {
          title: '操作',
          key: 'action',
          width: 120,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.edit(params.index)
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.remove(params.index)
                  }
                }
              }, '删除')
            ]);
          }
        }
      ],
    }
  },
  methods: {
    edit(index) {
      this.formItem = {
        name: this.toBuyData[index].name,
        price: this.toBuyData[index].price,
        priced: this.toBuyData[index].priced,
        index
      }
      this.modal = true
    },
    remove(index) {
      this.$emit('remove', index)
    },
    ok() {
      this.$emit('changeItem', this.formItem)
      this.formItem = {}
    },
    cancel() {
      this.$Message.info('取消修改物品!');
    }
  }
}
</script>

