<template>
  <div>
    <Table :toBuyData="toBuyData" v-on:remove="removeItem" v-on:changeItem="changeItem" />
    <Button type="primary" size="large" shape="circle" icon="android-add" class="add" @click="modalAdd = true" />
    <Modal v-model="modalAdd" title="新增物品" @on-ok="ok" @on-cancel="cancel">
      <Form :model="formItemAdd" :label-width="60">
        <Form-item label="选择场景">
            <Select v-model="formItemAdd.select" placeholder="请选择">
                <Option value="客厅">客厅</Option>
                <Option value="餐厅">餐厅</Option>
                <Option value="厨房">厨房</Option>
                <Option value="阳台">阳台</Option>
                <Option value="洗手间">洗手间</Option>
                <Option value="大房间">大房间</Option>
                <Option value="小房间">小房间</Option>
                <Option value="书房">书房</Option>
            </Select>
        </Form-item>
        <Form-item label="物品名称">
          <Input v-model="formItemAdd.name" placeholder="请输入"></Input>
        </Form-item>
        <Form-item label="预算价格">
          <Input v-model="formItemAdd.price" placeholder="请输入"></Input>
        </Form-item>
        <Form-item label="实际价格">
          <Input v-model="formItemAdd.priced" placeholder="请输入"></Input>
        </Form-item>
      </Form>
    </Modal>
    <footer>
      <div class="total-price">总预算: {{totalPrice}}</div>
      <div class="total-priced">总价格: {{totalPriced}}</div>
    </footer>
  </div>
</template>

<script>
import Table from './count/Table'
export default {
  name: 'app',
  data () {
    return {
      toBuyData: [],
      modalAdd: false,
      formItemAdd: {
        name: '',
        price: '',
        priced: '',
        select: ''
      },
    }
  },
  mounted () {
    if (localStorage.getItem('TOBUYDATA-V1')) {
      this.toBuyData = JSON.parse(localStorage.getItem('TOBUYDATA-V1'))
    }
  },

  computed: {
    totalPrice: function() {
      let total = 0
      this.toBuyData.forEach(item => {
        total += Number(item.price)
      })
      return total
    },
    totalPriced: function() {
      let total = 0
      this.toBuyData.forEach(item => {
        total += Number(item.priced)
      })
      return total
    }
  },
  methods: {
    removeItem(index) {
      this.toBuyData.splice(index, 1);
      localStorage.setItem('TOBUYDATA-V1', JSON.stringify(this.toBuyData))
      this.$Message.info('删除成功!');
    },
    changeItem({index, name, price, priced}) {
      this.toBuyData[index].name = name
      this.toBuyData[index].price = price
      this.toBuyData[index].priced = priced
      localStorage.setItem('TOBUYDATA-V1', JSON.stringify(this.toBuyData))
      this.$Message.info('修改成功!');
    },
    ok() {
      this.toBuyData.push(this.formItemAdd)
      localStorage.setItem('TOBUYDATA-V1', JSON.stringify(this.toBuyData))
      this.formItemAdd = {}
      this.$Message.info('添加成功!');
    },
    cancel() {
      this.$Message.info('取消添加新物品!');
    }
  },
  components: {
    Table
  }
}
</script>

<style>
.add {
  position: fixed;
  bottom: 30px;
  left: 48%;
  z-index: 1000;
}

footer {
  background-color: #030303;
  color: #FFF;
  width: 100%;
  height: 40px;
  position: fixed;
  bottom: 0;
  z-index: 999;
}

.total-price{
  line-height: 40px;
  width: 49%;
  display: inline-block;
  text-align: center;
}

.total-priced{
  line-height: 40px;
  width: 50%;
  display: inline-block;
  text-align: center;
}
</style>



